.header {
    max-width: 1280px;
    width: 95%;
    display: flex;
    align-items: center;
    gap: 10px;
    margin: 30px auto;
  
    span {
      font-weight: bold;
    }
  
    .header__icons {
      list-style: none;
      display: flex;
      align-items: center;
      gap: 10px;
      margin: 0 0 0 10px;
      padding: 0;
  
      li a {
        width: 32px;
        height: 32px;
        background-color: black;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
  
        svg {
          fill: white;
        }
      }
    }
  }
  